import {request} from "../../request.js";
export default {
	data() {
		return {
			account_number_list:[],
		};
	},
	onLoad() {
		this.getAccountNumberList();
	},
	methods:{
		bankCard(){
			uni.showLoading({
				title:"加载中",
				icon:"loading",
				mask:true
			})
			uni.navigateTo({
				url:"../bankcard/bankcard"
			});
		},
		getAccountNumberList(){
			request({
				url:"index.php?s=/wap/member/getBankList"
			}).then(res=>{
				this.account_number_list=res.data;
			})
		}
	}
}
